  margin-top: 6rem;
  margin-bottom: 18rem;
}
.chapter-page__container {
  display: grid;
  grid-template-columns: 5fr 7fr;
  column-gap: 8rem;
}
.chapter-page__aside {
  display: flex;
  flex-direction: column;
  min-width: 0;
}
.chapter-page__video-wrap {
  position: relative;
  margin-bottom: 4.7rem;
  height: 69.5rem;
  border-radius: 2rem;
  overflow: hidden;
}
.chapter-page__video {
  width: 100%;
  height: 100%;
}
.chapter-page__video video {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.chapter-page__image-wrap {
  margin-bottom: 4.7rem;
  height: 69.4rem;
  border-radius: 2rem;
  overflow: hidden;
}
.chapter-page__image {
  height: 100%;
  object-fit: cover;
}
.chapter-page__carousel {
  position: relative;
  margin-bottom: 6rem;
  height: 69.4rem;
  border-radius: 2rem;
  overflow: hidden;
}
.chapter-page__slider.swiper {
  margin: 0;
  height: 100%;
  min-width: 0;
}
.chapter-page__slide-img-wrap {
  height: 100%;
}
.chapter-page__slide-img {
  height: 100%;
  object-fit: cover;
}
.chapter-page__slider-pagination, .shop-card__slider-pagination {
  position: absolute;
  z-index: 2;
  bottom: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 5.6rem;
  border-radius: 0 0 2rem 2rem;
  background-color: rgba(28, 28, 28, 0.2);
  -webkit-backdrop-filter: blur(0.2rem);
          backdrop-filter: blur(0.2rem);
}
.chapter-page__slider-pagination .pagination__bullets, .shop-card__slider-pagination .pagination__bullets {
  position: static;
  transform: none;
}
.chapter-page__slider-pagination .pagination__bullets .swiper-pagination-bullet, .shop-card__slider-pagination .pagination__bullets .swiper-pagination-bullet {
  cursor: pointer;
}
.chapter-page__aside-group {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.chapter-page__shop-logo-wrap {
  width: 17.5rem;
}
.chapter-page__shop-logo {
  object-fit: contain;
}
.chapter-page__link .link__text {
  max-width: 14.4rem;
}
.chapter-page__content {
  display: flex;
  flex-direction: column;
}
.chapter-page__title {
  margin-bottom: 4.6rem;
  max-width: 80rem;
}
.articles-chapter-page .chapter-page__title {
  max-width: 94.5rem;
}
.chapter-page__labels {
  margin-bottom: 4.6rem;
  display: flex;
  column-gap: 1.6rem;
}
.chapter-page__text-wrap {
  display: flex;
  flex-direction: column;
  row-gap: 1rem;
}
.chapter-page__text-wrap_margin {
  margin-bottom: 3.2rem;
}
.chapter-page__text {
  color: #808080;
}
.shop-chapter-page .chapter-page__text {
  color: #1c1c1c;
}
.chapter-page__text-content {
  display: flex;
  flex-direction: column;
  row-gap: 4.6rem;
}
.chapter-page__text-item {
  display: flex;
  flex-direction: column;
  row-gap: 3.2rem;
}
.chapter-page__title-wrap {
  margin-bottom: 3rem;
  display: flex;
  align-items: center;
  column-gap: 3.6rem;
}
.chapter-page__title-wrap .chapter-page__title {
  margin: 0;
  text-transform: uppercase;
}
.chapter-page__title-icon-wrap {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  flex: 0 0 9.3rem;
  width: 9.3rem;
  height: 9.3rem;
  border-radius: 1rem;
  background-color: #ffffff;
}
.chapter-page__title-icon {
  flex: 0 0 6rem;
  width: 6rem;
  height: 6rem;
  object-fit: contain;
}
.chapter-page__video-modal {
  position: fixed;
  z-index: 100;
  top: 110%;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: #e6e6e6;
  transition: top 0.4s ease;
}
._video-modal-opened .chapter-page__video-modal {
  top: 0;
}
.chapter-page__video-modal-inner {
  padding: 2rem;
  display: flex;
  flex-direction: column;
}
.chapter-page__video-modal-close {
